@import url("https://fonts.googleapis.com/css2?family=Monda&display=swap");

* {
  font-family: "Monda", sans-serif;
}

body {
  background-color: #181824;
}

.app {
  .logo__holder {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      font-size: 96px;
      line-height: 156px;
      letter-spacing: 0.1em;
      padding: 25px 40px;

      color: white;
    }

    .auth {
      font-size: 25px;
      line-height: 156px;
      letter-spacing: 0.1em;
      padding: 25px 40px;

      a {
        text-decoration: none;
        margin-right: 35px;
        color: white;

        transition: all 0.3s ease-in-out;
      }

      a:hover {
        color: #ad02fe;
      }
    }
  }

  .input__holder {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;

    .input {
      display: flex;
      align-items: center;
      background: #25273c;
      border-top-left-radius: 30px;
      border-bottom-left-radius: 30px;
      width: 90vh;

      .input__text {
        font-size: 36px;
        line-height: 58px;
        letter-spacing: 0.1em;
        color: rgba(255, 255, 255, 0.7);
        width: 100%;
        margin-right: 50px;
        padding-left: 30px;
        outline: none;
        border: none;
        background-color: transparent;
      }
    }

    .input__btn {
      height: 69px;
      cursor: pointer;
    }
  }

  .list__holder {
    display: flex;
    align-items: center;
    flex-direction: column;

    .score {
      border: 2px solid #ad02fe;
      display: flex;
      justify-content: space-around;
      width: 97vh;

      font-size: 24px;
      letter-spacing: 0.1em;
      padding: 10px;
      color: white;

      .right {
        a {
          text-decoration: none;
          color: white;
          transition: all 0.2s ease-in-out;
          margin-left: 25px;
          cursor: pointer;
        }

        a:hover {
          color: #ad02fe;
        }
      }
    }

    .list {
      background-color: #25273c;
      width: 100vh;

      .list__item {
        display: flex;
        align-items: center;
        margin: 15px 0px;

        .text {
          font-size: 36px;
          line-height: 59px;
          letter-spacing: 0.1em;
          color: rgba(255, 255, 255, 0.7);
          margin: 0 auto;
          width: 75%;
          outline: none;
          border: none;
          background-color: transparent;

          border-bottom: 4px solid rgba(72, 64, 64, 0.37);
        }

        .faXmarkCircle {
          transition: all 0.3s ease-in-out;
          height: 46px;
          cursor: pointer;
        }

        .faXmarkCircle:hover {
          background-color: #ad02fe;
          border-radius: 100%;
        }

        .icon {
          margin: 0px 26px;
          display: flex;
          gap: 15px;

          img {
            cursor: pointer;
            height: 46px;
          }
        }
      }
    }
  }
}
